@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
}

.btn-login {
  border: none;
  background-color: rgb(173, 220, 145);
  height: 40px;
  width: 150px;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 15px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
}

.body {
  background-color: #f7f8f9;
}

.login-page {
  margin-top: 50px;
}

.login {
  margin: 20px;
  padding: 25px;
  border: 3px solid rgb(173, 220, 145);
  border-radius: 25px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login form input {
  margin: 10px;
  height: 28px;
  width: 195px;
  font-size: 20px;
  font-weight: 600;
  border-radius: 5px;
  border: 3px solid rgb(173, 220, 145);
}

.login form input::-webkit-input-placeholder {
  padding: 10px;
}

.login form input:-ms-input-placeholder {
  padding: 10px;
}

.login form input::placeholder {
  padding: 10px;
}

.text-login {
  color: rgb(173, 220, 145);
  font-size: 30px;
  font-weight: 900;
}

.text-signup {
  color: rgb(65, 181, 232);
  font-weight: bold;
  font-size: 10px;
}

.btn-signup {
  border: none;
  background-color: rgb(65, 181, 232);
  height: 40px;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 15px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  width: 150px;
  cursor: pointer;
  position: relative;
}

.btn-signup a {
  color: white;
  font-size: 15px;
  position: absolute;
  top: 10px;
  left: 50px;
}

.signup-page {
  margin-top: 70px;
}

.signup {
  margin: 20px;
  padding: 25px;
  border: 3px solid rgb(65, 181, 232);
  border-radius: 25px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.signup h2 {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  color: rgb(65, 181, 232);
}

.signup form {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Open Sans', sans-serif;
}

.signup form input {
  margin: 10px 0;
  height: 28px;
  width: 215px;
  border: 2px solid rgb(65, 181, 232);
  border-radius: 5px;
}

.signup form input::-webkit-input-placeholder {
  padding: 0 10px;
}

.signup form input:-ms-input-placeholder {
  padding: 0 10px;
}

.signup form input::placeholder {
  padding: 0 10px;
}

.signup form button {
  border: none;
  background-color: rgb(65, 181, 232);
  height: 40px;
  padding: 0 20px;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  width: 150px;
  cursor: pointer;
  margin: 20px 0;
}

.text {
  color: rgb(173, 220, 145);
  font-size: 15px;
}

.text-login a {
  color: rgb(173, 220, 145);
  font-size: 20px;
  font-weight: 900;
  text-decoration: underline;
}

.errors {
  margin-top: 25px;
  color: red;
}

.not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid yellow;
  border-radius: 25px;
  margin: 80px;
  padding: 10px;
}

.nf-title {
  font-size: 25px;
}

header {
  display: flex;
  justify-content: space-evenly;
  background: rgb(65, 181, 232);
  font-family: 'Open Sans', sans-serif;
  max-height: 7%;
  margin: 0;
  padding: 10px;
  position: relative;
}

a {
  text-decoration: none;
}

.logo-title {
  color: white;
  letter-spacing: 0.6px;
  font-family: 'Open Sans', sans-serif;
  font-size: 5vw;
  font-weight: bold;
  margin-left: 20%;
}

.logged-item {
  display: none;
}

.add-item {
  color: white;
  font-weight: bold;
  font-size: 7vw;
  position: absolute;
  left: 5%;
  top: 0;
}

.user-profile {
  position: absolute;
  top: 44%;
  right: 17%;
  color: white;
}

.logged-user-name {
  color: white;
  font-size: 2.5vw;
  position: absolute;
  right: 5%;
  top: 19%;
  cursor: pointer;
}

@media (max-width: 768px) {
  .user-profile {
    top: 38%;
    right: 15%;
  }
}

@media (max-width: 425px) {
  .user-profile {
    top: 53%;
    right: 2%;
  }
}

.footer {
  width: 100%;
  height: 10vh;
  background-color: transparent;
}

.calendar {
  display: flex;
  justify-content: center;
  padding-top: 100px;
}

.react-calendar {
  border: 5px solid rgb(65, 181, 232) !important;
  border-radius: 25px;
}

.add-track {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid orange;
  border-radius: 25px;
  margin: 10px 10%;
  padding: 10px;
}

.add-track h4 {
  color: orange;
}

.activ-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 20px;
}

.form-text input {
  border: 1px solid orange;
}

.number-form input {
  border: 1px solid orange;
  width: 50px;
}

.submit-btn {
  border: none;
  background-color: orange;
  height: 40px;
  width: 150px;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 15px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
}

.tracks-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tracks-list h3 {
  color: orange;
}

.track {
  background-color: whitesmoke;
  border-radius: 25px;
  margin-bottom: 10px;
  width: 90%;
  padding: 10px;
  cursor: pointer;
}

.one-train {
  font-weight: 700;
}

.activ {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.track-date {
  margin-left: 50px;
  font-size: xx-large;
  color: orange;
}

.progress-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
}

.progress-page-title {
  padding: 20px;
}

